import IReportRow from "./report-row";

function isOFZPK(row: IReportRow) {
    return isOFZ(row, "ПК");
}

export function isOFZ(row: IReportRow, type: string) {
    return row.sector === 1 && row.name.substr(0, 7) === `ОФЗ-${type} `;
}

export function isVarCoupon(row: IReportRow) {
    return row.couponValues.length > 1 || isOFZPK(row);
}

export function hasUnknownCoupon(row: IReportRow) {
    const v = row.couponValues;
    return v[v.length - 1] === null || isOFZPK(row);
}

export function calcCurrentYield(row: IReportRow) {
    const coupon = row.couponValues[0];
    const waprice = row.waprice;

    if (coupon && waprice) {
        return 100 * coupon / waprice;
    }

    return 0;
}

export function ratingText(row: IReportRow): string {
    switch (row.rating) {
        case 6: return "AAA";

        case 5.1: return "AA+";
        case 5.0: return "AA";
        case 4.9: return "AA-";

        case 4.1: return "A+";
        case 4.0: return "A";
        case 3.9: return "A-";

        case 3.1: return "BBB+";
        case 3.0: return "BBB";
        case 2.9: return "BBB-";

        case 2.1: return "BB+";
        case 2.0: return "BB";
        case 1.9: return "BB-";

        case 1.1: return "B+";
        case 1.0: return "B";
        case 0.9: return "B-";
    }
    return "";
}

export function toTsvLine(row: IReportRow) {
    const cells = [
        row.isin,
        pad(row.name, 12),
        tags(),
        row.listLevel,
        pad(ratingText(row), 4),
        row.maturityDate,
        numeric(row.waprice),
        couponStar(),
        numeric(row.couponValues[0]),
        numeric(row.closeYield)
    ];

    function tags(): string {
        const list = [ ];

        if (row.isQualOnly) {
            list.push("кв");
        } else if (row.inABI) {
            list.push("*");
        } else if (row.inCBHY) {
            list.push("вд");
        }

        if (row.isHighRisk) {
            list.push("пир");
        }

        return pad(list.join(", "), 7);
    }

    function couponStar(): string {
        if (isVarCoupon(row) || hasUnknownCoupon(row)) {
            return "**";
        }
        return "";
    }

    function numeric(value: number): string {
        return pad(String(value || ""), 8, true);
    }

    function pad(text: string, len: number, left = false): string {
        while (text.length < len) {
            if (left) {
                text = " " + text;
            } else {
                text = text + " ";
            }
        }
        return text;
    }

    return cells.join("\t");
}
