import Vue from "vue";
import "./bond-row-component";
import loadReport from "./load-report";
import nightMode from "./night-mode";
import { toTsvLine } from "./report-row-extensions";
import { load as loadSettings, minRatingModApplicable } from "./settings";
import { getMinCurrentYield, setMinCurrentYield } from "./settings";
import storageUtil from "./storageUtil";
import { applySettings, IViewModel } from "./view-model";

const LAUNCH_TIME = Date.now();
const ROOT_ID = "vue-root";
const cordova = (window as any).cordova;
const ALERT_DISMISS_KEY = "alert-v1";

const VM: IViewModel = {
    nightMode: nightMode.getMode(),
    collapseForm: window.matchMedia && window.matchMedia("(max-width: 992px)").matches
};

nightMode.updateMarkup();

if (cordova) {
    document.addEventListener("deviceready", onCordovaReady, false);
    document.addEventListener("deviceconfigchange", onCordovaConfigChange, false);
    document.documentElement.style.userSelect = "none";
    trackExternalLinks();
    ensureFocusedInputVisible();
} else {
    disableFocusedInputWheel();
    filterNumberInputKeys();
}

loadReport((report) => {
    VM.settings = loadSettings();
    VM.reportDate = report.date;
    VM.reportRows = report.rows;

    const reportError = report.error;
    VM.reportError = reportError;
    // VM.showAlert = !reportError && !storageUtil.load(ALERT_DISMISS_KEY);

    applySettings(VM);

    new Vue({
        el: "#" + ROOT_ID,
        data: VM,
        watch: {
            nightMode: {
                handler: (value) => nightMode.setMode(value)
            },
            settings: {
                deep: true,
                handler: () => applySettings(VM)
            }
        },
        methods: {
            alertClose: () => storageUtil.save(ALERT_DISMISS_KEY, true),
            clipboardButtonClick
        },
        computed: {
            minRatingModVisible() {
                return minRatingModApplicable(Number(this.settings.minRating));
            },
            minCurrentYieldThunk: {
                get() {
                    return getMinCurrentYield(this.settings);
                },
                set(value: string) {
                    setMinCurrentYield(this.settings, value);
                }
            }
        }
    });

    document.getElementById(ROOT_ID).style.display = "";
    document.getElementById("bond-finder-loading").style.display = "none";

    if (!cordova) {
        initFontSize();
    }
});

function onCordovaReady() {
    document.addEventListener("resume", onCordovaResume, false);
}

function onCordovaResume() {
    if (Date.now() - LAUNCH_TIME > 1234 * 1000) {
        location.reload();
    }
}

function onCordovaConfigChange() {
    nightMode.updateMarkup();
}

function initFontSize() {
    const html = document.documentElement;
    html.style.fontSize = "medium";
    html.style.fontSize = 1600 / parseFloat(getComputedStyle(html).fontSize) + "%";
}

function trackExternalLinks() {
    document.addEventListener("click", (e) => {
        const a = e.target as HTMLAnchorElement;
        if (a.tagName === "A" && !confirmExternalLink(a.href)) {
            e.preventDefault();
            e.stopPropagation();
        }
    }, false);
}

function ensureFocusedInputVisible() {
    document.addEventListener("click", (e) => {
        const element = e.target as HTMLElement;
        if (isInput(element)) {
            const { bottom } = element.getBoundingClientRect();
            const delta = bottom - innerHeight / 2;
            if (delta > 0) {
                try {
                    scrollBy({ behavior: "smooth", top: delta });
                } catch {
                    scrollBy(0, delta);
                }
            }
        }
    }, false);
}

function disableFocusedInputWheel() {
    function handler(e: WheelEvent) {
        const element = e.target as HTMLElement;
        if (document.activeElement === element && isInput(element)) {
            e.preventDefault();
        }
    }
    document.addEventListener("wheel", handler, { passive: false });
}

function filterNumberInputKeys() {
    const ALLOWED_CHARS = {
        ".": true,
        "-": true
    };

    for (let i = 0; i < 10; i++) {
        ALLOWED_CHARS[i] = true;
    }

    function handler(e: KeyboardEvent) {
        const element = e.target as HTMLInputElement;
        if (isInput(element) && element.type === "number") {
            const key = e.key || String.fromCharCode(e.keyCode);
            if (!ALLOWED_CHARS[key]) {
                e.preventDefault();
            }
        }
    }

    document.addEventListener("keypress", handler, { passive: false });
}

function isInput(element: HTMLElement) {
    return element.tagName === "INPUT";
}

function confirmExternalLink(href: string) {
    if (!href) {
        return true;
    }

    const kind = href.indexOf("moex.com/") > -1 ? "moex" : "other";
    const storageKey = "confirm-external-link-" + kind;

    if (!storageUtil.load(storageKey)) {
        const subj = kind === "moex"
            ? "сайт Московской Биржи"
            : "внешний сайт";
        if (!confirm("Пожалуйста, подтвердите переход на " + subj)) {
            return false;
        }
        storageUtil.save(storageKey, "1");
    }

    return true;
}

function clipboardButtonClick() {
    const tsvBuilder = [ ];
    for (const r of VM.reportRows) {
        if (r.fits) {
            tsvBuilder.push(toTsvLine(r));
        }
    }

    const tsv = tsvBuilder.join("\n");

    function onSuccess() {
        if (!cordova) {
            VM.showClipboardCheck = true;
        }
    }

    function onFailure() {
        alert("Не получилось");
    }

    if (cordova) {
        cordova.exec(onSuccess, onFailure, "CordovaPluginUtils", "clipboardWrite", [ tsv ]);
    } else {
        try {
            navigator.clipboard.writeText(tsv).then(onSuccess, onFailure);
        } catch {
            onFailure();
        }
    }
}
