import IReportRow from "./report-row";
import { calcCurrentYield, hasUnknownCoupon, isOFZ, isVarCoupon } from "./report-row-extensions";
import { ISettings, minRatingModApplicable, save as saveSettings } from "./settings";
import { getMinCurrentYield } from "./settings";

export interface IViewModel {
    nightMode: number;
    settings?: ISettings;
    reportDate?: string;
    reportRows?: ReadonlyArray<IReportRow>;
    reportError?: boolean;
    totalFits?: number;
    showAll?: boolean;
    hasABI?: boolean;
    hasCBHY?: boolean;
    hasHighRisk?: boolean;
    hasQualOnly?: boolean;
    collapseForm?: boolean;
    showVarCouponNotice?: boolean;
    showUnknownCouponNotice?: boolean;
    showClipboardCheck?: boolean;
    // showAlert?: boolean;
}

export function applySettings(VM: IViewModel) {
    saveSettings(VM.settings);

    const settings = VM.settings;
    const rub = settings.rub;
    const maturityDateMin = [ clampYear(settings.min_year), settings.min_month, "01" ].join("-");
    const maturityDateMax = [ clampYear(settings.year), settings.month, "31" ].join("-");
    const maxListLevel = Number(settings.maxListLevel);
    const minRating = Number(settings.minRating);
    const minRatingMod = Number(settings.minRatingMod);
    const maxPerYear = Number(settings.maxPerYear);
    const requiredCouponMonth = Number(settings.requiredCouponMonth);
    const maxAmort = Number(settings.maxAmort);
    const minTotalTradesPerc = Number(settings.minTotalTradesPerc);
    const minCurrentYield = Number(getMinCurrentYield(settings));

    const couponType = Number(settings.couponType);
    const allowVarCoupon = couponType > 0;
    const allowUnknownCoupon = couponType > 1;

    const minRatingCombo = minRatingModApplicable(minRating) ? (minRating + minRatingMod) : minRating;

    const TOP = 20;
    const MAX = 1000;

    let totalFits = 0;
    let hasABI = false;
    let hasCBHY = false;
    let hasHighRisk = false;
    let hasQualOnly = false;
    let showVarCouponNotice = false;
    let showUnknownCouponNotice = false;

    let ofzPKUrlAssigned = false;
    let ofzINUrlAssigned = false;

    for (const row of VM.reportRows) {
        if (fits(row)) {
            totalFits++;
            row.fits = true;
            row.top = totalFits <= TOP;
            showVarCouponNotice = showVarCouponNotice || isVarCoupon(row);
            showUnknownCouponNotice = showUnknownCouponNotice || hasUnknownCoupon(row);
            hasABI = hasABI || row.inABI;
            hasCBHY = hasCBHY || row.inCBHY;
            hasHighRisk = hasHighRisk || row.isHighRisk;
            hasQualOnly = hasQualOnly || row.isQualOnly;

            row.infoUrl = null;
            if (!row.closeYield) {
                if (!ofzPKUrlAssigned && isOFZ(row, "ПК")) {
                    ofzPKUrlAssigned = true;
                    row.infoUrl = "https://bcs-express.ru/novosti-i-analitika/ofz-pk-s-priviazkoi-k-ruonia-kak-zashchitit-sredstva-ot-infliatsii";
                } else if (!ofzINUrlAssigned && isOFZ(row, "ИН")) {
                    ofzINUrlAssigned = true;
                    row.infoUrl = "https://bcs-express.ru/novosti-i-analitika/ofz-in-ili-kak-garantirovano-zashchitit-sebia-ot-infliatsii";
                }
            }
        } else {
            row.fits = row.top = false;
        }
        if (totalFits >= MAX) {
            break;
        }
    }

    VM.totalFits = totalFits;
    VM.showAll = totalFits / TOP < 1.5;
    VM.hasABI = hasABI;
    VM.hasCBHY = hasCBHY;
    VM.hasHighRisk = hasHighRisk;
    VM.hasQualOnly = hasQualOnly;
    VM.showVarCouponNotice = showVarCouponNotice;
    VM.showUnknownCouponNotice = showUnknownCouponNotice;
    VM.showClipboardCheck = false;

    function fits(row: IReportRow) {
        if (row.pending) {
            return false;
        }

        if (row.rub !== rub) {
            return false;
        }

        if (row.isHighRisk && !settings.highRisk) {
            return false;
        }

        if (row.isQualOnly && !settings.qual) {
            return false;
        }

        if (maturityDateMin.length < 10 || maturityDateMax.length < 10) {
            return false;
        }

        if (row.maturityDate < maturityDateMin || row.maturityDate > maturityDateMax) {
            return false;
        }

        if (rub) {
            if (settings.sector1 || settings.sector2 || settings.sector3) {
                if (row.sector === 1 && !settings.sector1) { return false; }
                if (row.sector === 2 && !settings.sector2) { return false; }
                if (row.sector === 3 && !settings.sector3) { return false; }
            }

            if (row.listLevel > maxListLevel) {
                return false;
            }
        }

        if (row.rating < minRatingCombo) {
            return false;
        }

        const isMonthlyCoupon = row.couponMonths[0] === -12;

        if (maxPerYear > -1) {
            if (isMonthlyCoupon) {
                return false;
            }
            if (row.couponMonths.length > maxPerYear) {
                return false;
            }
        }

        if (maxPerYear === -12 && !isMonthlyCoupon) {
            return false;
        }

        if (requiredCouponMonth > 0 && !isMonthlyCoupon) {
            if (row.couponMonths.indexOf(requiredCouponMonth) < 0) {
                return false;
            }
        }

        if (!allowVarCoupon && isVarCoupon(row)) {
            return false;
        }

        if (!allowUnknownCoupon && hasUnknownCoupon(row)) {
            return false;
        }

        if (minCurrentYield > 0 && calcCurrentYield(row) < minCurrentYield) {
            return false;
        }

        if (maxAmort > -90 && row.minDaysBetweenAmort > 0) {
            if (maxAmort === -1) {
                return false;
            }
            if (row.minDaysBetweenAmort < maxAmort) {
                return false;
            }
        }

        if (maxAmort <= -90) {
            if (row.amortRangeDays > -maxAmort) {
                return false;
            }
        }

        if (row.totalTradesPerc < minTotalTradesPerc) {
            return false;
        }

        return true;
    }
}

function clampYear(year: string): string {
    year = String(year);
    if (year.length < 4) {
        return "2000";
    }
    if (year.length > 4) {
        return "3000";
    }
    return year;
}
